	import api from '../../common/api.js';
	import uniLoadMore from "../../components/uni-load-more/uni-load-more.vue"
	export default{
	    components: {uniLoadMore},
		data() {
			return {
	            ismore:false,
	            contentText: {
	                contentdown: '查看更多',
	                contentrefresh: '加载中',
	                contentnomore: '没有更多',
	            },
	            page:1,
	            size:10,
                
				communityId: '',        //424856479015406202
				id: uni.getStorageSync('userinfo').id,
				videoList: [],
				curIdx: null
			}
		},
		onLoad(option) {
			this.communityId = option.communityId
			this.getMyvideoList()
		},
        onPullDownRefresh() {
            this.page = 1
            this.getMyvideoList()
            
        },
        onReachBottom() {
            this.status = 'loading'
            uni.showNavigationBarLoading()
            
            this.page++
            this.getMyvideoList()
        },
		methods: {
            clickPlay:function(index){
                // this.videoList[index].play = true
                console.log(index)
                this.videoList.forEach((i, idx) => {
                	if (index !== idx) {
                        i.play = false
                		var videoContextOther = wx.createVideoContext('myVideo' + idx)
                		videoContextOther.pause()
                	}else{
                        i.play = true
                        this.$nextTick(function(){
                            var videoContextOther = wx.createVideoContext('myVideo' + idx)
                            videoContextOther.play()
                        })
                        
                    }
                })
            },
			getMyvideoList() {
				api.getMyvideoList({
                    page:this.page,
                    size:this.size,
					communityId: this.communityId
				}).then(res=>{
                    if(res.status == 'OK'){
                        if(this.page == 1){
                            this.videoList = []
                            if(res.list.length>=this.size){
                                this.ismore = true
                            }
                        }
                        res.list.map(item => {
                            item.play = false
                            this.videoList.push(item)
                        })
                    }
					uni.stopPullDownRefresh();
					uni.hideNavigationBarLoading()
				})
			},
			praiseAdd(id, index) {
				api.praiseAdd({
					videoId: id
				}).then(res=>{
					// this.getMyhvideo()
					this.videoList[index].praiseStatus = !this.videoList[index].praiseStatus
				})
			},
			praiseCancel(id, index) {
				api.praiseCancel({
					videoId: id
				}).then(res=>{
					// this.getMyhvideo()
					this.videoList[index].praiseStatus = !this.videoList[index].praiseStatus
				})
			},
			videoMarkAdd(id, index) {
				api.videoMarkAdd({
					videoId: id
				}).then(res=>{
					// this.getMyhvideo()
					this.videoList[index].markStatus = !this.videoList[index].markStatus
				})
			},
			videoMarkCancel(id, index) {
				api.videoMarkCancel({
					videoId: id
				}).then(res=>{
					// this.getMyhvideo()
					this.videoList[index].markStatus = !this.videoList[index].markStatus
				})
			},
			videoErrorCallback: function(e) {
                console.log(e.target.errMsg)
				// uni.showModal({
				// 	content: e.target.errMsg,
				// 	showCancel: false
				// })
			},
			goInfo(id) {
				uni.navigateTo({
					url: `/pages/liveborad/liveInfo?id=${id}`
				})
			},
			onShareAppMessage (options) {
				let item = options.target.dataset.item
				return {
					title: item.title,
					path: `pages/liveborad/share?id=${item.id}&communityId=${this.communityId}`,
					imageUrl: item.cover
				}
			},
			getMyhvideo(id) {
				api.getMyhVideo({
					id: id
				}).then(res=>{
					this.formData = res.data
				})
			},
			onPlay (e) {
				let curIdx = e.target.dataset.index
				this.videoList.forEach((i, idx) => {
					if (curIdx !== idx) {
                        i.play = false
						var videoContextOther = wx.createVideoContext('myVideo' + idx)
						videoContextOther.pause()
					}else{
                        i.play = true
                    }
				})
			}
		}
	}
